ify-center font-bold \n         transition-all duration-200 cursor-pointer\n         border border-terminal-green/20 \n         bg-gradient-to-br from-black/90 to-gray-900/80\n         text-terminal-green\n         text-[clamp(0.75rem,2.5vw,1rem)];\n  text-shadow: 0 0 5px currentColor;\n}\n\n.cell:hover:not(.cell-hit):not(.cell-miss):not(.cell-sunk) {\n  @apply border-terminal-green/60 scale-105;\n  box-shadow: 0 0 12px currentColor;\n  background: linear-gradient(135deg, #111111, #222222);\n}\n\n.cell-empty svg {\n  @apply w-5 h-5 md:w-6 md:h-6;\n  opacity: 0.3;\n  stroke: #00aa00;\n}\n\n.cell-ship svg {\n  @apply w-4 h-4 md:w-6 md:h-6;\n  opacity: 0.8;\n  stroke: #00aa00;\n}\n\n.cell-miss svg {\n  @apply w-4 h-4 md:w-6 md:h-6;\n  opacity: 0.6;\n  stroke: #00aa00;\n}\n\n.cell-hit {\n  animation: red-glow-pulse 2s ease-in-out infinite;\n  box-shadow: 0 0 8px rgba(255, 85, 85, 0.4);\n}\n\n.cell-hit svg {\n  @apply w-4 h-4 md:w-6 md:h-6;\n  opacity: 0.9;\n  stroke: #ff5555;\n}\n\n.cell-sunk {\n  box-shadow: 0 0 10px rgba(255, 34, 34, 0.3);\n}\n\n.cell-sunk svg {\n  @apply w-4 h-4 md:w-6 md:h-6;\n  opacity: 0.7;\n  stroke: #ff2222;\n}\n\n.cell-disabled {\n  @apply opacity-50 cursor-not-allowed hover:scale-100 hover:shadow-none;\n}\n\n@keyframes red-glow-pulse {\n  0%,\n  100% {\n    box-shadow: 0 0 8px rgba(255, 85, 85, 0.4);\n  }\n  50% {\n    box-shadow: 0 0 15px rgba(255, 85, 85, 0.6);\n  }\n}\n",".board-section {\n  @apply bg-gradient-to-br from-black/80 to-gray-900/80 \n         rounded-none p-2 md:p-5 border-2 border-terminal-green/20;\n  margin: 0 -0.5rem;\n  box-shadow:\n    inset 0 0 20px rgba(0, 0, 0, 0.7),\n    0 0 30px rgba(0, 255, 0, 0.1),\n    0 0 0 1px rgba(0, 255, 0, 0.1);\n  position: relative;\n  overflow: hidden;\n}\n\n.board-section h2 {\n  @apply font-bold mb-3 md:mb-4 text-terminal-green uppercase tracking-wider;\n  font-size: clamp(0.9rem, 3.6vw, 1.2rem);\n  text-shadow: 0 0 10px #00ff00;\n  letter-spacing: 2px;\n  z-index: 2;\n}\n\n.board-grid {\n  @apply grid grid-cols-10 bg-black/60 p-2 sm:p-3 md:p-4;\n  min-height: 250px;\n  gap: 0.125rem;\n  border: 1px solid rgba(0, 255, 0, 0.2);\n  background: rgba(0, 20, 0, 0.6);\n  box-shadow: inset 0 0 30px rgba(0, 0, 0, 0.8);\n  position: relative;\n  z-index: 2;\n}\n\n.board-container {\n  @apply transition-all duration-300 w-full;\n  opacity: 0;\n  transform: translateY(10px);\n  pointer-events: none;\n}\n\n.board-container.active {\n  @apply block;\n  opacity: 1;\n  transform: translateY(0);\n  pointer-events: auto;\n  animation: fadeInUp 0.4s ease-out;\n}\n\n.board-container:not(.active) {\n  @apply hidden;\n}\n\n@keyframes fadeInUp {\n  from {\n    opacity: 0;\n    transform: translateY(10px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n",".tab-button {\n  @apply text-terminal-green/60 border-b-2 border-transparent \n         transition-all duration-200 uppercase tracking-wider;\n  font-size: clamp(0.6rem, 2.8vw, 0.85rem);\n  padding: 0.75rem 0.5rem;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  letter-spacing: 1px;\n  text-shadow: 0 0 3px currentColor;\n  background: linear-gradient(\n    to bottom,\n    transparent 80%,\n    rgba(0, 255, 0, 0.05)\n  );\n}\n\n.tab-button.active {\n  @apply text-terminal-green border-terminal-green;\n  background: linear-gradient(to bottom, transparent 50%, rgba(0, 255, 0, 0.2));\n  text-shadow: 0 0 8px #00ff00;\n  box-shadow: inset 0 -1px 0 0 #00ff00;\n}\n\n.tab-button:not(.active):hover {\n  @apply text-terminal-green/90 border-terminal-green/50;\n  background: linear-gradient(to bottom, transparent 70%, rgba(0, 255, 0, 0.1));\n  text-shadow: 0 0 5px #00aa00;\n}\n\n/* Tablet: 768px - 1023px */\n@media (min-width: 768px) and (max-width: 1023px) {\n  .tab-button {\n    @apply py-4 text-base;\n    font-size: 1rem;\n  }\n\n  .board-grid {\n    min-height: 320px;\n    gap: 0.375rem;\n  }\n\n  .board-section {\n    @apply p-4;\n  }\n\n  .board-section h2 {\n    font-size: 1.25rem;\n    @apply mb-4;\n  }\n\n  .cell {\n    font-size: clamp(0.7rem, 1.5vw, 0.9rem);\n  }\n\n  .cell svg {\n    @apply w-5 h-5;\n  }\n}\n","#new-game-btn {\n  @apply font-exo font-bold py-2 px-5 rounded\n         bg-terminal-green text-black\n         border-2 border-terminal-green-dim\n         transition-all duration-150 ease-out\n         flex items-center justify-center gap-2\n         mx-auto\n         hover:shadow-[0_0_12px_rgba(0,255,0,0.5)]\n         hover:-translate-y-0.5\n         active:translate-y-0.5\n         active:scale-95\n         active:shadow-[0_0_6px_rgba(0,255,0,0.3)];\n  letter-spacing: 0.5px;\n}\n\n@media (max-width: 640px) {\n  #new-game-btn {\n    @apply py-1.5 px-4 text-sm;\n  }\n}\n\n#new-game-btn:focus-visible {\n  @apply outline outline-2 outline-terminal-green outline-offset-2;\n}\n","@tailwind utilities;\n","@media (max-width: 639px) {\n  .board-grid {\n    gap: 0.125rem;\n    min-height: 250px;\n  }\n  .cell {\n    font-size: clamp(0.5rem, 2.5vw, 0.8rem);\n  }\n}\n","/* Tablet: 768px - 1023px */\n@media (min-width: 768px) and (max-width: 1099px) {\n  #game-status {\n    min-height: 2em !important;\n    font-size: 0.975rem !important;\n    margin-bottom: 0.5rem !important;\n  }\n\n  .board-grid {\n    min-height: 280px !important;\n    gap: 0.25rem !important;\n    padding: 0.75rem !important;\n  }\n\n  .tab-button {\n    @apply py-3 text-sm !important;\n  }\n\n  .board-section {\n    @apply p-3 !important;\n  }\n\n  .board-section h2 {\n    font-size: 1rem !important;\n    @apply mb-3 !important;\n  }\n\n  .cell svg {\n    width: 32px !important;\n    height: 32px !important;\n  }\n\n  header {\n    @apply mb-4 !important;\n  }\n\n  h1 {\n    @apply text-3xl mb-0 !important;\n  }\n\n  footer {\n    @apply mt-4 text-sm !important;\n  }\n}\n","@media (min-width: 1024px) {\n  .board-grid {\n    gap: 0.375rem;\n    min-height: 380px;\n  }\n}\n","@import 'tailwindcss/base';\n@import './base/reset.css';\n@import './base/typography.css';\n\n@import 'tailwindcss/components';\n@import './components/cells.css';\n@import './components/boards.css';\n@import './components/tabs.css';\n@import './components/buttons.css';\n\n@import 'tailwindcss/utilities';\n\n@import './layout/mobile.css';\n@import './layout/tablet.css';\n@import './layout/desktop.css';\n"],"sourceRoot":""}]);
